.App {
    text-align: center;
}

.Buttons button {

    margin: 10px 10px 10px 10px;
    background-color: #14c9c9;
    border: none;
    color: white;
    width: 80px; height: 80px;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius: 50%;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
}

.Tips {
    font-size: 50px;
}

.clear {
    font-size: 60px;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
}